.option {
  background: aliceblue;
  border: 1px solid #84c5fe;
  border-radius: 5px;
  padding: 8px 15px;
  font-size: 17px;
  margin-bottom: 15px;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.option:last-child {
  margin-bottom: 0px;
}

.option:hover {
  color: #004085;
  background: #cce5ff;
  border: 1px solid #b8daff;
}

.correct {
  color: #155724;
  background: #d4edda;
  border: 1px solid #c3e6cb;
}

.incorrect {
  color: #721c24;
  background: #f8d7da;
  border: 1px solid #f5c6cb;
}

 .disabled {
  pointer-events: none;
}

.icon {
  height: 26px;
  width: 26px;
  border: 2px solid transparent;
  border-radius: 50%;
  text-align: center;
  font-size: 13px;
  pointer-events: none;
  transition: all 0.3s ease;
  line-height: 24px;
}
.tick {
  color: #23903c;
  border-color: #23903c;
  background: #d4edda;
}

.cross {
  color: #a42834;
  background: #f8d7da;
  border-color: #a42834;
}
